import { Component, OnInit } from '@angular/core';
import { JobService } from 'src/app/job/job.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-em-job-manager',
  templateUrl: './em-job-manager.component.html',
  styleUrls: ['./em-job-manager.component.css']
})
export class EmJobManagerComponent implements OnInit {
  id: number;
  allJobs: [{}];

  constructor(
    private jobService: JobService,
    private actRouter: ActivatedRoute,
    private rout: Router,
  ) { }
  getEmoployerJobs() {
    this.jobService.getAllJobsEm(this.id).subscribe(
      res => {this.allJobs = res;
        console.log(res);
      }
    )
  }
  ngOnInit() {
    this.actRouter.params.subscribe(
      res => this.id = res['id']
    );
    console.log("id is " + this.id);
    this.getEmoployerJobs()
  }

  deleteJob(id) {
    this.jobService.deleteJob(id);
    this.getEmoployerJobs()
  }

}
