import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Employer } from './employer';


@Injectable({
  providedIn: 'root'
})
export class EmployerService {
  private burl = "http://localhost:3000/employers/";

  constructor(private http: HttpClient) { }

  getAllEmployer() {
    return this.http.get<Employer[]>(this.burl + "all")
  }
  getSpecificEmployer(id) {
    return this.http.get<any>(this.burl + "getSpecficEmployer/" + id)
  }

  updataEmploye(empId, employer) {
    return this.http.post<any>(this.burl + "edit/" + empId, employer)
  }
  imageUpload(upload) {
    return this.http.post<any>(this.burl + 'upload', upload)
  }

  getEmProfile(id) {
    return this.http.get<any>(this.burl + "profile/" + id)
  }
   savePassword(emp:any){
    return this.http.post<any>("http://localhost:3000/users/savepass/"+emp.accountId,emp)
  }
  getEmployerPass(id){
    return this.http.get<any>("http://localhost:3000/users/getpass/"+id)
  }
}


