import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../../auth/auth.service'
import {JobService} from '../../job.service';

@Component({
  selector: 'app-jb-single',
  templateUrl: './jb-single.component.html',
  styleUrls: ['./jb-single.component.css']
})
export class JbSingleComponent implements OnInit {
  data;
  company;

  constructor(
    private jobService: JobService,
    private route: Router,
    private paramroute: ActivatedRoute,
    public auth: AuthService
  ) {
  }

  ngOnInit() {
    this.paramroute.params.subscribe(
      a => {

        this.jobService.getJobById(a['jobId']).subscribe(
          data => {
            this.data = data['job'],
              this.company = data['company'];
            console.log(data['company']);
          })
      })
  }
}

