import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  err = 'user name or password is incorrect';
  private _url = 'http://localhost:3000/users/';
  private userType;

  constructor(private http: HttpClient, private router: Router) {
  }

  registerUser(user) {
    this.userType = user.userType;
    // console.log(this.userType);
    return this.http.post<any>(this._url + 'register', user);
  }

  loginUser(user) {
    this.http.post<any>(this._url + 'login', user).subscribe(
      res => this.userType = res.userType
    );
    return this.http.post<any>(this._url + 'login', user);
  }

  loggedIn() {
    // console.log(localStorage.getItem('token'));
    return !!localStorage.getItem('token');
  }

  getToken() {
    // console.log(localStorage.getItem('token'));
    return localStorage.getItem('token');
  }

  Logout() {
    this.router.navigate(['/auth/login']);
    localStorage.removeItem('token');
    localStorage.removeItem('accountId');
    localStorage.removeItem('_id');
  }

  isCandidate() {
    // console.log(this.userType);
    return this.userType;
  }
}
