import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'employerLocationfilter'
})
export class EmployerLocationfilterPipe implements PipeTransform {

  transform(employers: any, searchItem: String): any {
    if (!employers || !searchItem) {
      return employers
    }
    return employers.filter(
      employerRes => employerRes.employer.address.city.toLowerCase().indexOf(searchItem.toLowerCase()) !== -1);
  }
}
