import {Component, OnInit} from '@angular/core';
// import {Observable} from 'rxjs';
// import {candidateData} from '../candidate-dashboard/candidate-dashboard.component';
import {CandidateDashboardService} from '../candidate-dashboard.service';
// import {HttpClient} from '@angular/common/http';
// import {ActivatedRoute} from '@angular/router';
// import {ContenteditableDirective} from 'ng-contenteditable';
import {Router} from '@angular/router';

@Component({
  selector: 'app-ca-db-profile',
  templateUrl: './ca-db-profile.component.html',
  styleUrls: ['./ca-db-profile.component.css']
})
export class CaDbProfileComponent implements OnInit {
  // declaring some variables
  countryInfo = [];
  stateInfo = [];
  cityInfo = [];
  candidateData;

  // Class constructor and init methods
  constructor(
    public _ser: CandidateDashboardService,
    private _router: Router,
  ) {
    this.candidateData = this._ser.candidateData;

    if (this.candidateData.accountId == null) {
      this._router.navigate([this._router.url.replace(/[\w-]+$/, '')]);
    }

  }

  ngOnInit() {
    this.getCountries();
    console.log(this.candidateData);
  }

  // Methods
  upload(event, propertyName) {
    this._ser.uploadCandidateImage(event, this._ser.candidateData).subscribe(u => console.log(u), e => console.log(e));
  }

  // Countries - State - City
  getCountries() {
    // this.countryInfo = this.countries.countries;
    this._ser.getCountries().subscribe(
      d => this.countryInfo = d['Countries'],
      e => console.log(e)
    );
  }

  onChangeCountry(countryValue) {
    // console.log(this.countryInfo, countryValue);
    // value = ;
    this.stateInfo = this.countryInfo[/^\d+/.exec(countryValue)[0]].States;
    // this.cityInfo = this.stateInfo[0].Cities;
  }

  onChangeState(stateValue) {
    // console.log(this.stateInfo, stateValue);
    this.cityInfo = this.stateInfo[/^\d+/.exec(stateValue)[0]].Cities;
  }
}
