import {Component, OnInit} from '@angular/core';
import {CandidateDashboardService} from '../candidate-dashboard.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-ca-resume',
  templateUrl: './ca-resume.component.html',
  styleUrls: ['./ca-resume.component.css']
})
export class CaResumeComponent implements OnInit {
  public candidateData;

  addEdu: boolean;
  education = {
    'educationType': null,
    'institute': null,
    'degreeType': null,
    'percentage': null,
    'startYear': null,
    'endYear': null,
    'field': null,
    'description': null
  };

  addWorkExp: boolean;
  workExp = {
    'companyName': null,
    'startDate': null,
    'endDate': null,
    'jobTitle': null,
    'jobDescription': null
  };

  constructor(
    private _ser: CandidateDashboardService,
    private _router: Router
  ) {
    this.addEdu = false;
    this.addWorkExp = false;

    this.candidateData = this._ser.candidateData;
    if (this.candidateData.accountId == null) {
      this._router.navigate([this._router.url.replace(/[\w-]+$/, '')]);
    }
  }

  pushEdu() {
    if (Object.values(this.candidateData.education.university[0]).filter(v => !v).length === 8) {
      this.candidateData.education.university[0] = this.education;
    } else {
      this.candidateData.education.university.push(this.education);
    }
    this.candidateData.education.university.sort((a, b) => +b.startYear - +a.startYear);
    this.education = {
      'educationType': null,
      'institute': null,
      'degreeType': null,
      'percentage': null,
      'startYear': null,
      'endYear': null,
      'field': null,
      'description': null
    };
    this.addEdu = false;
  }

  pushWork() {
    if (Object.values(this.candidateData.experience.workExperience[0]).filter(v => !v).length === 5) {
      this.candidateData.experience.workExperience[0] = this.workExp;
    } else {
      this.candidateData.experience.workExperience.push(this.workExp);
    }
    this.workExp = {
      'companyName': null,
      'startDate': null,
      'endDate': null,
      'jobTitle': null,
      'jobDescription': null
    };
    this.candidateData.experience.workExperience.sort(
      (a, b) => +/\d+/.exec(b.startDate) - +/\d+/.exec(a.startDate));
    this.addWorkExp = false;
  }

  ngOnInit() {
  }

}
