import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
// import {HttpClient} from '@angular/common/http';
import {CandidateDashboardService} from '../candidate-dashboard.service';
import {AuthService} from '../../../auth/auth.service';
// import {TimeInterval} from 'rxjs';

const l = console.log;

@Component({
  selector: 'app-candidate-dashboard',
  templateUrl: './candidate-dashboard.component.html',
  styleUrls: ['./candidate-dashboard.component.css']
})
export class CandidateDashboardComponent implements OnInit {
  candidateData;
  private candidateId;

  // Class constructor and init methods
  constructor(
    public _ser: CandidateDashboardService,
    private _route: ActivatedRoute,
    private _auth: AuthService
  ) {
    this.candidateData = this._ser.candidateData;
    // console.log(this.candidateData);
  }

  save() {
    this._ser.saveCandidateChanges().subscribe(r => console.log(r), e => console.log(e));
  }

  cancel() {
    console.log(this.candidateData);
  }

  ngOnInit() {
    // console.log(!this._ser.candidateData['accountId'], localStorage.getItem('accountId'), this.candidateData._id);

    if (!this._ser.candidateData['accountId'] || localStorage.getItem('accountId') != this.candidateData.accountId) {
      this._route.params.subscribe(p => {
        this.candidateId = p._id;
        this._ser.getCurrentCandidateData(this.candidateId).subscribe(
          d => {
            // console.log(d, d['information'].gender === undefined, '----- init candidateData -----');
            if (d['information'].gender === undefined) {
              this.candidateData.information.name = d['information'].name;
              this.candidateData.information.email = d['information'].email;
              this.candidateData.information.mobile = d['information'].mobile;
              this.candidateData.accountId = d['accountId'];
              this.candidateData.candidateId = d['candidateId'];
              this.candidateData._id = d['_id'];
              this._ser.candidateData = this.candidateData;
              // console.log(this.candidateData, '----- init candidateData _id -----');
            } else {
              // this.candidateData = d;
              this._ser.candidateData = d;
              this.candidateData = this._ser.candidateData;
              // console.log(this.candidateData, this._ser.candidateData);
            }

            console.log(this.candidateData.information.gender);

            if (this.candidateData.profile.profileImage === 'default_profile_picture.jpg'
              && this.candidateData.information.gender == false) {
              this._ser.candidateData['profile'].profileImage = 'default_female_profile_picture.png';
              this.candidateData['profile'].profileImage = 'default_female_profile_picture.png';
            }

            if (this.candidateData.profile.profileImage === 'default_female_profile_picture.png'
              && this.candidateData.information.gender == true) {
              this._ser.candidateData['profile'].profileImage = 'default_profile_picture.jpg';
              this.candidateData['profile'].profileImage = 'default_profile_picture.jpg';
            }

            this.candidateData.education.university.sort(
              (a, b) => +b.startYear - +a.startYear);
            this.candidateData.experience.workExperience.sort(
              (a, b) => +/\d+/.exec(b.startDate) - +/\d+/.exec(a.startDate));
            this.candidateData.experience.skills.sort(
              (a, b) => +b.percentage - +a.percentage);
          },
          e => console.log(e));
      });
    }
  }

}
