import {Component, OnInit} from '@angular/core';
import {CandidateDashboardService} from '../../dashboard/candidate-dashboard.service';

const l = console.log;

@Component({
  selector: 'app-ca-operations',
  templateUrl: './ca-operations.component.html',
  styleUrls: ['./ca-operations.component.css']
})
export class CaOperationsComponent implements OnInit {


  public candidates;

  constructor(
    public _ser: CandidateDashboardService,
  ) {
    console.log(this.candidates);

    if (!this.candidates) {
      this._ser.getAllCandidates().subscribe(d => {
        this.candidates = d;
        this.candidates.forEach((v, i) => {
          this.candidates[i].information.address.country = v.information.address.country.replace(/\d+_/, '');
          this.candidates[i].information.address.state = v.information.address.state.replace(/\d+_/, '');
          // this.candidates[i].information.name.first = v.information.name.first.replace(/\b./g, l => l.toUpperCase());
          // this.candidates[i].information.name.last = v.information.name.last.replace(/\b./g, l => l.toUpperCase());
        });
        return console.log(d);
      }, e => console.log(e));

      // _ser.getAllCandidates().subscribe(d => {
      //   console.log(this.candidates, d);
      //   return this.candidates = d;
      // }, e => console.log(e));

    }
  }

  ngOnInit() {
  }

}
