import {Component, OnInit} from '@angular/core';
import {CandidateDashboardService} from '../candidate-dashboard.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-ca-applied-jobs',
  templateUrl: './ca-applied-jobs.component.html',
  styleUrls: ['./ca-applied-jobs.component.css']
})
export class CaAppliedJobsComponent implements OnInit {
  public candidateData;

  constructor(
    public _ser: CandidateDashboardService,
    private _router: Router
  ) {
    this.candidateData = this._ser.candidateData;
    if (this.candidateData.accountId == null) {
      this._router.navigate([this._router.url.replace(/[\w-]+$/, '')]);
    }
  }

  ngOnInit() {
  }

}
