import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { EmployerService } from '../../employer.service';
import { Employer } from '../../employer';
import { JobService } from 'src/app/job/job.service';

@Component({
  selector: 'app-em-profile',
  templateUrl: './em-profile.component.html',
  styleUrls: ['./em-profile.component.css']
})
export class EmProfileComponent implements OnInit {
  id: Number;
  state = false;
  // specificEmployer : Employer;
  specificEmployer: any;
  allJobs :[{}];
  // test :{name:{first:"";middle:"";last:""}}
  // specificEmployer : Employer =new Employer(1,this.test,this.test);

  constructor(
    private activateRouter: ActivatedRoute,
    private route: Router,
    private employerService: EmployerService,
    private jobService : JobService
  ) { }
  getSpecifEmploy() {
    if(this.route.url == '/profile/'+this.id) {
      this.state = true;
    this.employerService.getEmProfile(this.id).subscribe(
      reslt => {this.specificEmployer = reslt;
               console.log(reslt)
              }
    )
   } else {
    this.employerService.getSpecificEmployer(this.id).subscribe(
      reslt => {this.specificEmployer = reslt;
               console.log(reslt)
              }
    )
   }
  }

  getEmoployerJobs() {
    if(this.route.url == '/profile/'+this.id) {
    this.jobService.getJobsForEmp(this.id).subscribe(
      res =>this.allJobs = res
    )
    } else {
      this.jobService.getAllJobsEm(this.id).subscribe(
        res =>this.allJobs = res
      )
    }
  }
  ngOnInit() {
    console.log(this.route.url);
    this.activateRouter.params.subscribe(
      a => this.id = a['id']
    );
    this.getSpecifEmploy();
    this.getEmoployerJobs();
  }

}
