import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
// Employer
import {EmOperationsComponent} from './employer/search/em-operations/em-operations.component';
import {EmProfileComponent} from './employer/search/em-profile/em-profile.component';
import {EmJobManagerComponent} from './employer/dashboard/em-job-manager/em-job-manager.component';
import {EmDbProfileComponent} from './employer/dashboard/em-db-profile/em-db-profile.component';
import {EmChangePasswordComponent} from './employer/dashboard/em-change-password/em-change-password.component';
// Candidates
import {CaOperationsComponent} from './candidate/search/ca-operations/ca-operations.component';
import {CaProfileComponent} from './candidate/search/ca-profile/ca-profile.component';
// Candidate Dashboard
import {CaDbProfileComponent} from './candidate/dashboard/ca-db-profile/ca-db-profile.component';
import {CaCvCoverComponent} from './candidate/dashboard/ca-cv-cover/ca-cv-cover.component';
import {CaChangePasswordComponent} from './candidate/dashboard/ca-change-password/ca-change-password.component';
import {CaAppliedJobsComponent} from './candidate/dashboard/ca-applied-jobs/ca-applied-jobs.component';
import {CandidateDashboardComponent} from './candidate/dashboard/candidate-dashboard/candidate-dashboard.component';
import {CaDashboardComponent} from './candidate/dashboard/ca-dashboard/ca-dashboard.component';
import {CaResumeComponent} from './candidate/dashboard/ca-resume/ca-resume.component';
import {CaResumeNewComponent} from './candidate/dashboard/ca-resume-new/ca-resume-new.component';
// Jobs
import {JbListComponent} from './job/search/jb-list/jb-list.component';
import {JbGridComponent} from './job/search/jb-grid/jb-grid.component';
import {JbSingleComponent} from './job/search/jb-single/jb-single.component';
import {CreateJobComponent} from './job/create-job/create-job.component';
// Blogs
import {BlListComponent} from './blog/bl-list/bl-list.component';
import {BlSingleComponent} from './blog/bl-single/bl-single.component';
// Others
import {HomeComponent} from './home/home.component';
import {AboutUsComponent} from './others/about-us/about-us.component';
import {ErrorComponent} from './others/error/error.component';
import {ContactUsComponent} from './others/contact-us/contact-us.component';
import {FaqComponent} from './others/faq/faq.component';
import {HowItWorksComponent} from './others/how-it-works/how-it-works.component';
// import {LoginComponent} from './others/login/login.component';
// import {RegisterComponent} from './others/register/register.component';
import {TermsAndConditionsComponent} from './others/terms-and-conditions/terms-and-conditions.component';
// auth
import {RegisterComponent} from './auth/register/register.component';
import {LoginComponent} from './auth/login/login.component';
import {AuthGuard} from './auth.guard';
import {MainComponentComponent} from './employer/main-component/main-component.component';

const routes: Routes = [

  // Employer
  {
    path: 'employers', component: MainComponentComponent, children: [

      {path: 'profile/:id', component: EmProfileComponent, canActivate: [AuthGuard]},
      {
        path: 'dashboard/job-manager/:id', component: EmJobManagerComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'jobs/creatjob', component: CreateJobComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'jobs/edit/:jobId', component: CreateJobComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'dashboard/profile/:id', component: EmDbProfileComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'dashboard/change-password/:id', component: EmChangePasswordComponent,
        canActivate: [AuthGuard]
      }

    ], canActivate: [AuthGuard]
  },
  {path: 'profile/:id', component: EmProfileComponent},
  {path: 'employers/search', component: EmOperationsComponent},
  // {path: 'employers/profile/:id', component: EmProfileComponent,
  // canActivate: [AuthGuard]},
  // {path: 'employers/dashboard/job-manager', component: EmJobManagerComponent},
  // {path: 'employers/dashboard/post-new', component: EmPostNewComponent},
  // {path: 'employers/dashboard/profile/:id', component: EmDbProfileComponent,
  // canActivate: [AuthGuard]},
  // {path: 'employers/dashboard/change-password', component: EmChangePasswordComponent},

  // Candidates
  {path: 'candidates', component: CaOperationsComponent, pathMatch: 'full'},
  {path: 'candidates/:_id', component: CaProfileComponent},
  // Candidate Dashboard
  {
    path: 'candidates/:_id/dashboard', component: CandidateDashboardComponent,
    children: [
      {path: '', component: CaDashboardComponent},
      {path: 'profile', component: CaDbProfileComponent},
      {path: 'cv-cover', component: CaCvCoverComponent},
      {path: 'applied-jobs', component: CaAppliedJobsComponent},
      {path: 'change-password', component: CaChangePasswordComponent},
      {path: 'resume', component: CaResumeComponent},
      {path: 'new-resume', component: CaResumeNewComponent},
    ]
  },

  // Jobs
  {path: 'jobs/search/list', component: JbListComponent},
  {path: 'jobs/search/grid', component: JbGridComponent},
  {path: 'jobs/search/single/:jobId', component: JbSingleComponent},
  // {path: 'jobs/creatjob', component: CreateJobComponent},
  // {path: 'jobs/creatjob/:jobId', component: CreateJobComponent},

  // Blog
  {path: 'blogs/list', component: BlListComponent},
  {path: 'blogs/single', component: BlSingleComponent},

  // Others
  {path: 'home', component: HomeComponent},
  {path: 'about-us', component: AboutUsComponent},
  {path: 'error', component: ErrorComponent},
  {path: 'contact-us', component: ContactUsComponent},
  {path: 'faq', component: FaqComponent},
  {path: 'how-it-works', component: HowItWorksComponent},
  // {path: 'login', component: LoginComponent},
  // {path: 'register', component: RegisterComponent},
  {path: 'terms-and-conditions', component: TermsAndConditionsComponent},
  // { path: '', component:},
  {path: '', component: HomeComponent, pathMatch: 'full'},
  // {path: '**', redirectTo: '/error', pathMatch: 'full'},
  // auth
  {path: 'auth/login', component: LoginComponent},
  {path: 'auth/register/:id', component: RegisterComponent},


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
