import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class JobService {

 private jobs: any[] = [];
 private jobsUpdated = new Subject<any[]>();

  private burl = 'http://localhost:3000/jobs/';
  constructor(private http: HttpClient) { }

  getAllJobs() {
    this.http.get<any>(this.burl + 'all')
    .subscribe(data => {
      this.jobs = data;
      this.jobsUpdated.next([...this.jobs]);
    });
  }

  getJobUpdateListener() {
    return this.jobsUpdated.asObservable();
  }
// jobs for Employers


  addJob(job: any) {
    console.log(job);

    this.http.post<any>(this.burl + 'add', job).subscribe(a => {
           this.jobs.push(a);
           this.jobsUpdated.next([...this.jobs]);
    });
  }
  getJobById(id: any) {
    return this.http.get(this.burl + id);
  }

  getAllJobsEm(id: any) {
    return this.http.get<any>(this.burl + "employerJobs/" + id);
  }
  updateJob(job: any, jobId) {
    const newjob = job;
     this.http.post<any>(this.burl + '/edit/' + jobId, newjob).subscribe(data =>{
       console.log(data);
      const updatedJobs = [...this.jobs];
      console.log(updatedJobs);
      const oldJobIndex = updatedJobs.findIndex(p => p.jobId == jobId);
       updatedJobs[oldJobIndex] = data[0];
      this.jobs = updatedJobs;
      this.jobsUpdated.next([...this.jobs]);
    });
  }

  getJobsForEmp(id) {
    return this.http.get<any>(this.burl + "listJobs/" + id);
  }
  deleteJob(jobId) {
    this.http
      .get(this.burl + 'delete/' + jobId)
      .subscribe(() => {
        const  updatedJobs = this.jobs.filter(p => p.jobId != jobId);
        this.jobs = updatedJobs;
        this.jobsUpdated.next([...this.jobs]);
      });
    }
}
