import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'employerFilter'
})
export class EmployerFilterPipe implements PipeTransform {
  transform(employers: any, searchItem: String): any {
    if (!employers || !searchItem) {
      return employers
    }
    return employers.filter(
      employerRes => employerRes.employer.name.first.toLowerCase().indexOf(searchItem.toLowerCase()) !== -1);
  }
}
