import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';

import { CategoryService } from '../../categories/category.service';
import { JobService } from '../job.service';


@Component({
  selector: 'app-create-job',
  templateUrl: './create-job.component.html',
  styleUrls: ['./create-job.component.css']
})
export class CreateJobComponent implements OnInit {

  @ViewChild('jobfrm') pform: NgForm;
  id;
  jobType = 'Full Time';
  category = 1;
  state = 'true';
  level = 'Entry Level';
  categories;
  editMode = false;
  job;

  constructor(private route: Router,
              private jobService: JobService,
              private categoryService: CategoryService,
              private paramroute: ActivatedRoute,
              ) {
              }

  ngOnInit() {
    this.paramroute.params.subscribe(
      a => {
        if (a['jobId'] !== undefined) {
          this.editMode = true;
          console.log(a['jobId']);
         this.id = a['jobId'];
          this.jobService.getJobById(a['jobId']).subscribe(
            job => {
            this.job = job;
            console.log(job);
           let date = new Date(this.job.job.details.expireDate);
            this.pform.setValue({
              title: this.job.job.details.title,
              description: this.job.job.details.description,
              type: this.job.job.details.type,
              Category: this.job.job.details.categories,
              salary: this.job.job.details.offeredSalary.salary,
              state: this.job.job.details.offeredSalary.offeredSalaryState,
              level: this.job.job.details.level,
              date:  date.getFullYear() + '-' + ((date.getMonth() + 1 > 9 ?
              date.getMonth() + 1 : '0' + (date.getMonth() + 1))) + '-' + ( date.getDate() > 9 ? date.getDate() : '0' + date.getDate()),
              skill: this.job.job.details.skills.join(' '),
              position: this.job.job.details.offeredJobs
            })
           });
          }
      });

     this.categoryService.getCategories().subscribe(
       a => this.categories = a
     )

}

  onSubmitJob(form: NgForm) {

    if (form.invalid) {
      return;
  }
    if (this.editMode) {
      this.jobService.updateJob(form.value, this.id);
      // console.log(form.value, this.id);

     } else {
      let info = {job: form.value, empid: localStorage.getItem("accountId") };
       this.jobService.addJob(info);
     }

     this.editMode = false;
     this.route.navigate(['/employers/dashboard/job-manager/', localStorage.getItem("accountId")])

   }



}
