import {Component, OnInit} from '@angular/core';
import {AuthService} from '../auth.service';
import {ActivatedRoute, Router} from '@angular/router';
import {CandidateDashboardService} from "../../candidate/dashboard/candidate-dashboard.service";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  userData = {
    information: {name: {}},
    hr: {name: {}},
    manager: {name: {}},
    employer: {name: {}},
    userType: false
  };

  public id = false;

  public candidateData;

  constructor(
    public auth: AuthService,
    private activateRoute: ActivatedRoute,
    private router: Router,
    private _ser: CandidateDashboardService
  ) {
    this.candidateData = this._ser.candidateData;
  }

  ngOnInit() {
    this.activateRoute.params.subscribe((data) => {
      if (data['id'] === '0') {
        this.id = false;
      } else if (data['id'] === '1') {
        this.id = true;
      }
    });
    console.log(this.id);
  }

  userRegister() {
    this.userData.userType = this.id;
    // console.log(this.userData);
    this.auth.registerUser(this.userData).subscribe(
      res => {
        if (res.userType) {
          this.router.navigate(['/employers/profile', res.accountId]);
        } else {
          this.router.navigate(['candidates', res._id, 'dashboard']);
        }
        // console.log({...this.candidateData, ...res}, ' register res');
        localStorage.setItem('token', res.token);
        localStorage.setItem('accountId', res.accountId);
        localStorage.setItem('_id', res._id);
      },
      err => console.log(err)
    );
  }
}
