import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { JobService } from '../../job.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-jb-list',
  templateUrl: './jb-list.component.html',
  styleUrls: ['./jb-list.component.css']
})
export class JbListComponent implements OnInit, OnDestroy {
  jobs: any[] = [];
  searchText = '';
  subscription: Subscription;
  constructor(private route: Router, private jobService: JobService) {

  }

  ngOnInit() {

    this.getAllJobs();
  }

  getAllJobs() {
    this.jobService.getAllJobs();
    this.subscription = this.jobService.getJobUpdateListener()
      .subscribe((jobs: any[]) => {
        this.jobs = jobs;
        console.log(jobs);
      });
  }
  ngOnDestroy() {
    console.log("hi");
    this.subscription.unsubscribe();
  }

}
