import {Component, OnInit} from '@angular/core';
import {CandidateDashboardService} from '../../dashboard/candidate-dashboard.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-ca-profile',
  templateUrl: './ca-profile.component.html',
  styleUrls: ['./ca-profile.component.css']
})
export class CaProfileComponent implements OnInit {
  public candidate;

  constructor(
    private _ser: CandidateDashboardService,
    private _router: ActivatedRoute,
    // private _auth: AuthService
  ) {
    this._router.params.subscribe(p => {
      this._ser.getCurrentCandidateData(p._id).subscribe(d => {
        console.log(this.candidate);
        this.candidate = d;
        this.candidate.information.address.country = this.candidate.information.address.country.replace(/\d+_/, '');
        this.candidate.information.address.state = this.candidate.information.address.state.replace(/\d+_/, '');
      }, e => console.log(e));
    });
  }

  ngOnInit() {
  }

}
